import React, { useState, useEffect } from 'react';
import './App.css';

const Az = window.Az;

const App = () => {
  const [text, setText] = useState('');
  const [morphReady, setMorphReady] = useState(false);

  useEffect(() => Az.Morph.init('js/az/dicts', () => setMorphReady(true)), []);

  const isWerb = () => {
    let tema = '';
    if (morphReady && text.length > 0) {
      const tokens = Az.Tokens(text).done();

      tokens.map(token => {
        let isVerb = false;
        if (token.type.toString() === 'WORD') {
          const word = text.substr(token.st, token.length);
          const wordMorphied = Az.Morph(word)[0];

          if (wordMorphied && wordMorphied.hasOwnProperty('tag')) {
            if (wordMorphied.tag.hasOwnProperty('VERB') && wordMorphied.tag.VERB === true) {
              const winged = wordMorphied.word;
              isVerb = true;

              tema = `"Ну ${winged} и ${winged}"`;
            }
          }
        }
        return isVerb;
      });
    }

    return tema;
  }

  return (
    <div className="app">
      <h1>Нейротатьяныч</h1>
      <img className="artemiy" width="300" src="/assets/artemiy.png" alt="Artemiy" />
      <h3 className="out">{isWerb()}</h3>
      <textarea
        placeholder="Введите новость"
        onChange={(event) => setText(event.target.value)}
        value={text}
      />
      <footer>
        <span>By <a href="https://ksed.dev">Ksedline</a>© in {new Date().getFullYear()}</span>
      </footer>
    </div>
  );
}

export { App };
